import React, { useEffect, useState } from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
import PropTypes from 'prop-types';
import InfiniteScroll from "react-infinite-scroll-component";
import { cleanup } from '@testing-library/react';


const News = (props)=>  {

  const[articles, setArticles] = useState([])
  const[loading, setLoading] = useState(true)
  const[page, setPage] = useState(1)
  const[totalResults, setTotalResults] = useState(0)  

const capitalizeFirstLetter=(string)=> {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

  const updateNews = async ()=>{
    props.setProgress(10);
    const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apikey}&page=${page}&pageSize=${props.pageSize}`;
    setLoading(true);
    props.setProgress(30);
    let data = await fetch(url);
    props.setProgress(60);
    let parseddata = await data.json();
    props.setProgress(80);
    setArticles(parseddata.articles)
    setTotalResults(parseddata.totalResults)
    setLoading(false)
    props.setProgress(100);
  }

  useEffect(() => {
    updateNews();
    document.title = `${capitalizeFirstLetter(props.category)} - NewsAven `;
  }, []);

  const handleprvclick = async ()=>{
    setPage(page -1);
    updateNews();
  }

  const handlenxtclick = async ()=>{
  setPage(page+1)
  updateNews();
  }

  const fetchMoreData = async  () => {
    const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apikey}&page=${page+1}&pageSize=${props.pageSize}`;
    setPage(page+1)
    let data = await fetch(url)
    let parseddata = await data.json();
    setArticles(articles.concat(parseddata.articles))
    setTotalResults(parseddata.totalResults)
  };

    
    return (
     <>
        <h1 className="text-center" style={{margin : '35px',marginTop: '90px'}}>NewsAven - Top Headlines on {capitalizeFirstLetter(props.category)}</h1>
        {loading && <Spinner/>}
        <InfiniteScroll
          dataLength={articles.length}
          next={fetchMoreData}
          hasMore={articles.length !== totalResults}
          loader={<Spinner/>}
        >
          <div className="container">
        <div className="row">
        {articles.map((element,index)=>{
          return <div className="col-md-4" key={index}>
          <NewsItem  title={element.title ? element.title : " "} description={element.description ? element.description : " "} imageurl={element.urlToImage} newsurl={element.url} author={element.author} date={element.publishedAt} source={element.source.name}/>
          </div>})}      
        </div>
        </div>
        </InfiniteScroll>
        
        
      </>  
    )
  }


News.defaultProps = {
  country : 'us',
  pageSize : 8,
  category : 'general'
}

News.propTypes = {
  country : PropTypes.string,
  pageSize : PropTypes.number,
  category : PropTypes.string,
}

export default News
